.headerOption {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 20px;
  color: gray;
  cursor: pointer;
}

.headerOption_icon {
  object-fit: contain;
  height: 25px !important;
  width: 25px !important;
}

.headerOption_title {
  font-size: 12px;
  font-weight: 400;
}

.headerOption:hover {
  color: #000;
}

@media only screen and (max-width: 412px) {
  .headerOption,
  .header__premium {
    display: none;
  }

  .profile_logout {
    top: 47px;
  }
}
